* {box-sizing:border-box}

.prev, .next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    margin-top: -22px;
    padding: 16px;
    color: white;
    font-weight: bold;
    font-size: 18px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    user-select: none;
  }

  .next {
    right: 0;
    border-radius: 3px 0 0 3px;
  }

  .prev:hover, .next:hover {
    background-color: rgba(0,0,0,0.8);
  }
.mySlides {
  width: 80vw;
  max-height: 450px;
  
}
.landmarkPhoto {
    max-width: 900px;
    max-height: 450px;
    overflow: hidden;
    margin: 0 auto;

}
@media only screen and (max-width: 1000px) {
    .landmarkPhoto {
        width: 80vw;
    }
}